import React from 'react';
import Highcharts from 'react-highcharts';
import Sticky from 'react-sticky';
import Btnitem from '../../../components/advertika/btnitem';
import SortableTable from '../../../components/react-sortable-table/sortable-table';
import Dropdown from '../../../components/advertika/dropdown';
import ReactTooltip from '../../../components/react-tooltip/react-tooltip';
import _ from 'underscore';

const PPchoiceSO = React.createClass({
    filter:{
        column: 'trend',
        sort: 'desc'
    },
    filterNextPage:{
        loadnext: true
    },    
    getInitialState() {
        return {
            data: [],
            page: 0,
            loading: false
        };
    },
    getDefaultProps() {
        return propsPPchoiceSO();
    },
    componentWillMount() {
        this.loadNext();
    },
    handleSort(column, sort) {
        this.filter.column = column;
        this.filter.sort = sort;      
        this.filter.page = this.state.page - 1;  
        this.setState({ loading: true });         
        $.getJSON(this.props.url, this.filter, result => {
            if (this.isMounted()) {
                this.setState({
                    totalGoals: result.totalOffers,
                    loading: false,
                    data: result.offers
                });
            }            
            this.filterNextPage = _.extend(this.filterNextPage, this.filter);;
        });
    },      
    loadNext() {
        this.filterNextPage.page = this.state.page;
        this.setState({ loading: true });         
        $.getJSON(this.props.url, this.filterNextPage, (result) => {
            if (this.isMounted()) {
                this.setState({                    
                    totalOffers: result.totalOffers,
                    page: this.state.page + 1,                   
                    loading: false,
                });
                this.setState({ data: this.state.data.concat(result.offers) })
            }
        });
    },    
    render() {

        return (<div>
                    {this.state.loading
                        ? <div className="progress" style={{'position':'fixed', 'zIndex': 999, top: -10, left: 0 }}>
                            <div className="indeterminate"></div>
                        </div> 
                        : null} 
                    {this.state.data.length

                        ?<div>
                            <SortableTable 
                                className="tbl __fzsm __center"
                                onSort={this.handleSort}
                                data={this.state.data} 
                                columns={this.props.tableColumns}
                                />
                            
                            <div className="center mb4">
                                { this.state.data.length != this.state.totalOffers
                                ?<Btnitem
                                    className="__primary"
                                    onClick={ this.loadNext }
                                    disabled={ this.state.loading }>
                                    {(!this.state.loading ? 'Загрузить еще' : 'Загрузка')}
                                </Btnitem>
                                :null}
                                {this.state.data.length
                                    ?<div>
                                        <small>
                                            {"Отображается "+ this.state.data.length + " из " + this.state.totalOffers}
                                        </small>
                                    </div>
                                    :null}
                            </div> 
                        </div>
                        : !this.state.loading
                            ?<div className='note-user' >
                                <span className='ion-android-search'></span>
                                <span className='text'>
                                    Похожих офферов не найдено.
                                </span>
                            </div>
                            : null
                        }

                    <ReactTooltip place='top' type='light' effect='float' multiline={true}/>
                </div>   
        );
    }
});



export default PPchoiceSO


