import React, { PropTypes } from 'react'

const CellImageListDD = React.createClass({
    getDefaultProps() {
        return {
            limit : 1
        };
    },
    getInitialState() {
        return {
            open: false
        };
    },
    _outsideClick(e) {
        var isClickInside = React.findDOMNode(this).contains(e.target);
        if (!isClickInside) {
            this.setState({
                open: false
            });
        document.removeEventListener("click", this._outsideClick);
        }
    },
    handleClick() {

        this.setState({
            open: !this.state.open
        });
        document.addEventListener("click", this._outsideClick);
    },
    render () {
        var listItem = (data, i)=><img key={i}  className="img-responsive" src={data.img} style={{maxWidth: 100}} alt={data.title} data-tip={data.title}/>;
        var opener = (this.props.data.length > this.props.limit ? <span onClick={this.handleClick} data-tip="" className="cell-dropdown_toggle"></span> : null);
        var ddContent = this.state.open
                       ? <div className="cell-dropdown_cnt">
                            
                                {this.props.data.slice(this.props.limit).map((data, i)=>listItem(data, i))}
                            
                        </div>
                        : null;
        return (
        <div className={'cell-dropdown ' + (this.state.open ? '__open' : '')}>
            <div className="cell-dropdown_header" style={this.props.data.length > this.props.limit ? null :{padding: 0}}>
                {this.props.data.slice(0,this.props.limit).map((item, i)=>listItem(item, i))}
                {this.props.data.length > this.props.limit 
                ?<span style={{display: 'inline-block', width: 24, textAlign: 'center', verticalAlign: 'middle'}}>
                    {this.props.data.length}
                </span>
                : null}
                {opener}
            </div>
            {ddContent}

        </div>
        )
    }
})

export default CellImageListDD



