import React from 'react';

const CellTrend = React.createClass({


    render() {
        var diffUser = this.props.data.diffUser;
        var className = 'txt-success';
        var trend = '';

        if (diffUser != 0) {
            if ((/\-/gi).test(diffUser)) {
                className = 'txt-danger';
            } else {                
                trend = '+';
            } 
        };

        return (<span>
                    {this.props.data.countUser}
                    {this.props.data.diffUser 
                    ?<span className={className}>
                        {` (${trend}${diffUser})`}
                    </span>
                    :null}
                </span>
              
        )
    }
})

export default CellTrend



