import React from 'react';
import Highcharts from 'react-highcharts';
import _ from 'underscore';
import SortableTable from '../../../../components/react-sortable-table/sortable-table';
import Dropdown from '../../../../components/advertika/dropdown';

const ChartAndTable = React.createClass({
    getInitialState() {
        return {
            dropdown: {

                    items: this.props.dropdownData
                },
            chartData: this.props.chartData
        }
    },
    handleToggleSeries(name) {
        this.props.chartData.forEach(c=>c.visible = c.name == name ? !c.visible : c.visible)
        this.setState({chartData:this.props.chartData});
    },

    toggleChekbox(unuse, checkedInputs) {

        this.state.chartData.forEach(c => {
            c.checked = c.visible = checkedInputs.indexOf(c.name) != -1 ? true : false;
        })
        this.setState({
            chartData: this.state.chartData
        });
    },
    refresh(){
        this.setState({
            dropdown: {

                    items: this.props.dropdownData
                },
            chartData: this.props.chartData
        });
        this.refs.dropdown.refresh();
    },

    render() {
        var chartData = this.state.chartData;


        var checkboxes = chartData
                            ? chartData
                                .filter(c=>c['checked'])
                                // .slice(0,5)
                                .map((c,i)=>
                                    <label key={c.name} className="cart-checkbox py1">
                                        <input
                                            type="checkbox"
                                            checked={c['visible']}
                                            onChange={this.handleToggleSeries.bind(this, c.name)}/>
                                        <span style={c['visible']?{background: c['color']}:null}/>
                                        {c.name}
                                    </label>
                                    )
                            : null;

        var highChartData = _.extend({}, this.props.chartConfig);
        highChartData.series = chartData;


        return (
            <section style={this.props.style}>
                <h3>{this.props.children}</h3>
                {this.props.chartData.length !== 0
                ?<div>
                    <div className="row px2">
                        <div style={{ display: 'inline-block'}} className="left col-15 py1">
                             <Dropdown
                                ref="dropdown"
                                items={this.state.dropdown}
                                /*subtitle={this.props.filterSubTitle}*/
                                onChange={this.toggleChekbox}
                                design="link"
                                noControls="true"
                                positionClass="__ddright"
                                subtitle="+ Добавить"
                                noControls="true"
                            />
                        </div>
                        <div className="left col-85">
                            {checkboxes}
                        </div>


                    </div>
                    <Highcharts config={highChartData} />
                    <SortableTable className="tbl __center" data={this.props.data || []} columns={this.props.columns}/>
                </div>
                :<div className='note-user' >
                        <span className='ion-android-search'></span>
                        <span className='text'>
                            Недостаточно данных для построения графиков.
                        </span>
                </div>}

            </section>
        );
    }
});

export default ChartAndTable;



