import React from 'react';
import Highcharts from 'react-highcharts';
import Sticky from 'react-sticky';
import ReactTooltip from '../../../components/react-tooltip/react-tooltip'
import SortableTable from '../../../components/react-sortable-table/sortable-table';
import Btn from '../../../components/advertika/btnitem';

const PPchoicePP = React.createClass({
    getInitialState() {
        return statePPchoicePL();
    },
    getDefaultProps() {
        return propsPPchoicePL();
    },
    componentWillMount() {
        this.loadNext('1');
        
        setTimeout(()=>{
            this.loadNext('2', true)
        }, 100);
    },
    showLnd(){
        this.setState({tblToggle:true});
    },
    showPln(){
        this.setState({tblToggle:false});
        ga('send', 'pageview', '/virtual/prelending');
    },
    loadNext(numtab, endloading) {
        
        this.setState({ loading: true });
        $.getJSON(this.props['url'+ numtab], {page: this.state['page'+ numtab]}, result => {
            if (this.isMounted()) {
                this.setState({
                    ['totalOffers'+numtab]: result.totalCount,
                    ['page' + numtab]: this.state['page'+numtab] + 1,
                    ['data' + numtab]: this.state['data'+numtab].concat(result.dataList),
                    access: result.access
                });
                if (endloading) {
                    this.setState({loading: false});
                };
            }
        });
    },
    render() {
        var numtab = this.state.tblToggle ? '1' : '2';
        var controls = this.state.access && this.state.data1.length || this.state.data2.length
                            ?<div className="a-right">
                                <Btn 
                                    className={"__sm " + (this.state.tblToggle ? "__primary" : "")} 
                                    onClick={this.showLnd}>
                                    Лендинги
                                </Btn>
                                <Btn 
                                    className={"__sm " + (this.state.tblToggle ? "" : "__primary")} 
                                    onClick={this.showPln}>
                                    Прелендинги
                                </Btn>
                            </div>
                            :null;        
        var content = (this.state['data' + numtab].length > 0) && this.state.access
                    ?<div>
                        <SortableTable
                            className="tbl __center"
                            data={ this.state[ 'data' + numtab] }
                            columns={this.props[ 'tableColumns' + numtab]}
                        />

                        <div className="center mb4">

                            { this.state[ 'data' + numtab].length != this.state['totalOffers'+ numtab]
                                ?<Btn
                                    className="__primary"
                                    onClick={ this.loadNext.bind(this, numtab, true) }
                                    disabled={ this.state.loading }>
                                    {(!this.state.loading ? 'Загрузить еще' : 'Загрузка')}
                                </Btn>
                                :null}

                            {this.state[ 'data' + numtab].length
                                ?<div>
                                    <small>
                                        {`Отображается ${this.state[ 'data' + numtab].length} из ${this.state['totalOffers'+ numtab]}`}
                                    </small>
                                </div>
                                :null}

                        </div>
                        <ReactTooltip place='top' type='light' effect='float' multiline={true}/>
                    </div>

                    :null;
        return (
                <div>
                    {this.state.loading
                        ? <div className="progress" style={{'position':'fixed', 'zIndex': 999, top: -10, left: 0 }}>
                            <div className="indeterminate"></div>
                        </div>
                        : null}

                    {this.state.access
                        ?<div>
                            {controls}                        
                            {content}
                        </div>
                        :<div className='no-acces-bg'>
                            <span>
                                Для Вашего тарифа информация по посадочным страницам оффера отсутствует. 
                                Для получения полного доступа перейдите на тариф Всё включено.
                            </span>
                            <a className='[ button __success __lg ]' href='/cabinet/info/tarif'>
                                Выбрать платный тариф
                            </a>
                        </div>}

                    {!this.state.loading && (this.state['data' + numtab].length === 0)
                        ?<div className='note-user' >
                            <span className='ion-android-search'></span>
                            <span className='text'>
                                Отсутствуют данные по данному офферу.
                            </span>
                        </div>
                        : null}
                </div>
        );
    }
});



export default PPchoicePP


