import React from 'react';
import Tabs from 'react-simpletabs';
import PPchoiceAnalytics from './ppchoice/ppchoiceAnalytics';
import PPchoicePP from './ppchoice/ppchoicePP';
import PPchoiceAds from './ppchoice/ppchoiceAds';
import PPchoiceLP from './ppchoice/ppchoiceLP';
import PPchoiceSO from './ppchoice/ppchoiceSO';

const PPchoice = React.createClass({
    getInitialState() {
        return {
            tabActive: +location.hash.substr(1) || 2,
            data: statePPсhoice(),
        };
    },
    render() {
        return (
            <Tabs tabActive={this.state.tabActive}>
                <Tabs.Panel title='Аналитика' >
                    <PPchoiceAnalytics />
                </Tabs.Panel>
                <Tabs.Panel title='Партнерские программы'>
                    <PPchoicePP />
                </Tabs.Panel>
                <Tabs.Panel title='Объявления'>
                    <PPchoiceAds />
                </Tabs.Panel>
                <Tabs.Panel title='Посадочные страницы'>
                    <PPchoiceLP />
                </Tabs.Panel>
                <Tabs.Panel title='Похожие офферы'>
                    <PPchoiceSO />
                </Tabs.Panel>
            </Tabs>
        );
    }
});

React.render(<PPchoice/>, document.getElementById('ppchoicetabs'));



