import React, { PropTypes } from 'react';

const CellGeoDD = React.createClass({
    getDefaultProps() {
        return {
            limit : 1
        };
    },
    getInitialState() {
        return {
            open: false
        };
    },
    _outsideClick(e) {
        var isClickInside = React.findDOMNode(this).contains(e.target);
        if (!isClickInside) {
            this.setState({open: false});
            document.removeEventListener("click", this._outsideClick);
        }
    }, 
    componentWillUnmount() {
        document.removeEventListener("click", this._outsideClick);
    },
    handleClick() {
        this.setState({open: !this.state.open });        
        document.addEventListener("click", this._outsideClick);
    },
    render() {
        var listItem = (geo, i)=><span className={'i-geo ' + geo.img} key={i} style={{verticalAlign: 'middle'}} data-tip={geo.country}/>;
        var opener = (this.props.data.length > this.props.limit ? <span onClick={this.handleClick} data-tip="" className="cell-dropdown_toggle"></span> : null);
        var ddContent = this.state.open
                        ? <div className="cell-dropdown_cnt">
                            <div>
                                {this.props.data.slice(this.props.limit).map((geo, i)=>listItem(geo, i))}
                            </div>
                        </div>
                        : null;
        return (
        <div className={'cell-dropdown ' + (this.state.open ? '__open' : '')}>
            <div className="cell-dropdown_header">
                {this.props.data.slice(0,this.props.limit).map((geo, i)=>listItem(geo, i))}
                {this.props.data.length > this.props.limit 
                ?<span style={{display: 'inline-block', width: 24, textAlign: 'center', verticalAlign: 'middle'}}>
                    {this.props.data.length}
                </span>
                : null}
                {opener}
            </div>
            {ddContent}
            

        </div>
        )
    }
})

export default CellGeoDD



