import React, { PropTypes } from 'react';

const CellGeoList = React.createClass({
    getDefaultProps() {
        return {
            limit : 2
        };
    },
    handleOpenAnchor(e) {
        var win = window.open(
            `${$(e.target).closest('tr').children().first().find('a').attr('href')}#${this.props.anchor}`,
            '_blank');
        win.focus();        
    },
    render () {
        var  declOfNum = (number, titles) =>{ //http://www.vanchester.ru/collections/code/php/skript-skloneniya-chislitelnyh-PHP-i-JavaScript
            var cases = [2, 0, 1, 1, 1, 2];
            return titles[(number % 100 > 4 && number % 100 < 20) ? 2 : cases[(number % 10 < 5) ? number % 10 : 5]];
        };
        var countries = ['страна', 'страны', 'стран'];
        var also = this.props.data.length - this.props.limit;
        var listItem = (item, i) => 
                    <li key={item.code} >
                        <span                 
                            className={'i-geo ' + item.code.toLowerCase()}
                            style={{verticalAlign: 'middle', marginRight: 5}} 
                            data-tip={item.title}
                        />
                        
                        {` - ${item.percent} %`}
                    </li>;
        
        return (
                <ul>            
                        {this.props.data.slice(0, this.props.limit).map((item, i)=>listItem(item, i))}
                        {this.props.data.length > this.props.limit
                            ?<div 
                                onClick={this.handleOpenAnchor}
                                style={{cursor:'pointer'}}
                                className="txt-primary">
                                {/*`и еще ${also} ${declOfNum(also, countries)}`*/}
                                еще страны
                            </div>
                            :null}
                </ul>
        )
    }
})

export default CellGeoList



