import React, { PropTypes } from 'react';

const PriceVK = React.createClass({
    getDefaultProps() {
        return {
            limit : 2
        };
    },
    render () {        
        var listItems = (item) => 
                    <li key={item}>
                        <span                 
                            className={`i-${item}-exchange`}
                            style={{verticalAlign: 'middle', marginRight: 5}} 
                        />
                        {this.props.data[item]}
                    </li>;
        
        return (
                <ul>
                {Object.keys(this.props.data).length
                    ?Object.keys(this.props.data).map(c => listItems(c))
                    :<li className="center">{this.props.txt||'нет на биржах'}</li>
                }
                </ul>
        )
    }
})

export default PriceVK



