import React from 'react';
// import ReactTooltip from '../../../components/react-tooltip/react-tooltip';

const PPchoiceAds = React.createClass({
  getInitialState() {
    return PPchoicePLConfig();
  },

  loadData(){
    return $.post(this.state.url,this.state.param);
  },

  componentWillMount() {

    if (!PPchoicePLData) {
      this.loadData()
        .then(
          data => {
            PPchoicePLData = data;
            this.forceUpdate()
          }
        );
    }
  },

  componentDidUpdate() {
    Tipped.create('.tipped', function() {
      return {
        content: $(this).data('content')
      };
    });
  },

  render() {

    if (!PPchoicePLData) {
      return (
          <div className="progress" style={{'position':'fixed', 'zIndex': 999, top: -10, left: 0 }}>
            <div className="indeterminate"></div>
          </div>
      )
    }

    return (
      <div>
        {PPchoicePLData.length < 60
          ? <div className="m4 p4 center h3">Объявлений не найдено</div>
          : <div dangerouslySetInnerHTML={{__html:PPchoicePLData}} />}

      </div>
    );
  }
});

export default PPchoiceAds


