import React from 'react';
import _ from 'underscore';
import moment  from 'moment';
import Highcharts from 'react-highcharts';
import Sticky from 'react-sticky';
import Dropdown from '../../../components/advertika/dropdown';
import Datepicker from '../../../components/advertika/Datepicker';
import ChartAndTable from './ppchoiceAnalytics/ChartAndTable';
import ReactTooltip from '../../../components/react-tooltip/react-tooltip'

import SortableTable from '../../../components/react-sortable-table/sortable-table';



const PPchoiceAnalytics = React.createClass({
  filter:{
    dateStart: moment().subtract(29, 'days').format('DD.MM.YYYY'),
    dateEnd: moment().format('DD.MM.YYYY')
  },
  getInitialState() {

    return {
      loaded: false,
      access: true,
      series1: [],
      series2: [],
      series3: [],
      series4: [],
      series5: [],
      data1: [],
      data2: []
    };
  },
  getDefaultProps() {
    return propsPPchoiceAn();
  },
  componentWillMount() {
    this.applyFilter();
  },
  resetFilters(){
    this.filter = {
      dateStart: moment().subtract(29, 'days').format('DD.MM.YYYY'),
      dateEnd: moment().format('DD.MM.YYYY')
    };
    this.props.dropdowns.forEach((c,i)=>this.refs[i].handleChange(()=>false));
    this.refs.datepicker.reset();
    setTimeout(()=>this.applyFilter(),100);
  },
  changeFilter(key, val) {
    this.filter[key] = val;
  },
  setDate(start, end) {
    this.filter.dateStart = start;
    this.filter.dateEnd = end;
  },
  handleToggleChart(series, i) {
    this.state['series'+ series][i]['visible'] = !this.state['series'+ series][i]['visible'];
    this.setState({['series'+ series] : this.state['series'+ series]});
  },
  applyFilter(config) {
    this.setState({ loading: true });
    $.getJSON(this.props.url, this.filter, (result)=>{
      if (result.access != false) {
        if (this.isMounted()) {
          this.setState({
            access: true,
            loaded: true,
            loading: false,
            series1:result.snAndGeo.series1,
            series2:result.snAndGeo.series2,
            data1:result.snAndGeo.data1,
            data2:result.snAndGeo.data2,
            series3:result.dmgr.series3,
            series4:result.dmgr.series4,
            series5:result.dmgr.series5,
          });
          this.refs.ads.refresh();
          this.refs.countries.refresh();
        }
      } else {
        this.setState({
          access: result.access,
          loaded: true,
          loading: false,
        })
      }
    });
  },
  render() {

    this.state.series1.forEach(
      (c, i) => {
        c.visible = i < 5;
        c.checked = c.visible;
      }
    );

    this.state.series2.forEach(
      (c, i) => {
        c.visible = i < 5;
        c.checked = c.visible;
      }
    );

    var chart3 = _.extend({}, this.props.HighchartDemographyMan);
    var chart4 = _.extend({}, this.props.HighchartDemography);
    var chart5 = _.extend({}, this.props.HighchartDemographyWoman);

    chart3.series =  this.state.series3;
    chart4.series =  this.state.series4;
    chart5.series =  this.state.series5;

    var dropdownData = (data) => data.map(
        c => {
          return{
            name: c.name,
            id: c.name,
            checked: c.visible,
            visible: c.visible,
          }
        }
      );
    return (
    <div>
      {this.state.loading
        ?<div className="progress" style={{'position':'fixed', 'zIndex': 999, top: -10, left: 0 }}>
          <div className="indeterminate"></div>
        </div>
        :null}

          {this.state.access
            ?this.state.loaded
            ?<div className="clearfix" style={{paddingTop: 1}}>
              <Sticky
                style={{'position':'absolute', 'zIndex': 10 }}
                stickyStyle={{'zIndex': 10, 'position': 'fixed', 'top': 0, 'left': 0, 'right': 0, 'minWidth': '1200px'}}
              >
                <div className="[ container ] fhp">
                  <div className="fhp_resetfilters" onClick={this.resetFilters}>
                    Очистить фильтры <i className="ion-close-circled"></i>
                  </div>
                    {this.props.dropdowns.map((item,i)=>(
                      <div className="left col-30" style={{marginRight: '5%'}} key={i}>
                        <Dropdown
                          {...item}
                          index={i}
                          ref={i}
                          items={item}
                          filterName={item.filterName}
                          title={item.title}
                          subtitle={item.subtitle}
                          onChange={this.changeFilter}
                          applySelected={this.applyFilter}/>
                      </div>
                    ))}
                    <div className="left col-30">

                      <Datepicker
                        ref="datepicker"
                        title="Период показа:"
                        setDate={this.setDate}
                        applyDate={this.applyFilter}
                        opens="left"
                      />

                    </div>

                </div>
              </Sticky>


              <ChartAndTable
                ref="ads"
                filterSubTitle="Рекламные сети"
                dropdownData={dropdownData(this.state.series1)}
                style={{marginTop: 140}}
                chartConfig={this.props.HighchartConfig}
                chartData={this.state.series1}
                data={this.state.data1}
                columns={this.props.columns1}
                series="1"
                onToggleChart={this.handleToggleChart}>
                Рекламные сети
                {' '}
                <i
                  className='ion-help-circled txt-primary'
                  data-tip='Рекламные сети, в которых были показаны объявления этого оффера. <br> Для просмотра графиков по другим рекламным  сетям измените <br>настройки фильтра по сетям.'/>
              </ChartAndTable>

              <ChartAndTable
                ref="countries"
                filterSubTitle="Страны"
                dropdownData={dropdownData(this.state.series2)}
                chartConfig={this.props.HighchartConfig}
                chartData={this.state.series2}
                data={this.state.data2 }
                columns={this.props.columns2}
                series="2"
                onToggleChart={this.handleToggleChart}>
                Страны
                {' '}
                <i
                  className='ion-help-circled txt-primary'
                  data-tip='Страны, в которых показываются объявления <br> этого оффера. Для просмотра графиков по другим <br> странам измените настройки фильтра по странам.'/>
              </ChartAndTable>


              { this.state.series4.length > 0 && !this.state.loading
                  ?<div>
                    <h3>
                      Демография
                      {' '}
                      <i
                        className='ion-help-circled txt-primary'
                        data-tip='Данные о структуре целевой аудитории <br> доступны для социальных сетей ВК и Таргет@mail.ru'/>
                    </h3>
                    <div className="clearfix">
                      <div className="left col-33">
                        <Highcharts config={chart3}/>
                      </div>
                      <div className="left col-33">
                        <Highcharts config={chart4}/>
                      </div>
                      <div className="left col-33">
                        <Highcharts config={chart5}/>
                      </div>
                    </div>
                  </div>
                  :<div className='note-user' >
                    <span className='ion-android-search'></span>
                    <span className='text'>
                      По рекламным сетям, в которых были показаны объявления этого оффера, демографические данные отсутствуют. Данные о структуре целевой аудитории доступны для социальных сетей ВК и myTarget.
                    </span>
                  </div>}

              <ReactTooltip place='top' type='light' effect='float' multiline={true}/>


            </div>

            :null

            :<div className='no-acces-bg'>
              <span>Для Вашего тарифа информация по аналитике отсутствует. Для получения полного доступа перейдите на тариф Всё включено.</span>
              <a className='[ button __success __lg ]' href='/cabinet/info/tarif'>Выбрать платный тариф</a>
            </div> }



    </div>
    );
  }
});



export default PPchoiceAnalytics;




